import dayjs from "dayjs"

export function reverseCalcDates(data) {
  let i,
    results = [],
    reverse_api_result = {}
  let now = new Date()
  let targetDays = 30
  const get_date = (date) => {
    return dayjs(date).format("YYYY-MM-DD")
  }

  data.map((data) => {
    return (reverse_api_result[data.date] = { count: data.count, errors: data.errors })
  })

  for (i = targetDays; i > 0; --i) {
    results.push({
      date: get_date(now),
      count: reverse_api_result[get_date(now)]["count"] || 0,
      errors: reverse_api_result[get_date(now)]["errors"] || 0,
    })

    now.setDate(now.getDate() - 1)
  }


  return results.reverse()
}
